}
.hover\:text-neutral-800:hover{
  --tw-text-opacity: 1;
  color: rgb(38 38 38 / var(--tw-text-opacity, 1));
}
.hover\:text-slate-100:hover{
  --tw-text-opacity: 1;
  color: rgb(241 245 249 / var(--tw-text-opacity, 1));
}
.focus\:outline-0:focus{
  outline-width: 0px;
}
